<template>
	<div class="Lotterypool_box">

		<div class="Lotterypool_head">
			<div class="Lotterypool_head_left animated flipInX">
				<el-input v-model="input" placeholder="根据名称搜索" style="margin-right: 10px;"></el-input>
				<el-button type="primary" icon="el-icon-search" size="mini" style="height: 40px;" @click='scrch'>搜索</el-button>
			</div>
			<div class="Lotterypool_head_right">
				<el-button type="text" style="padding: 5px 20px !important;">
					<el-upload class="upload-demo" action="" :on-change="handleChange" :on-remove="handleRemove" :on-exceed="handleExceed"
					 :limit="limitUpload" accept="application/vnd.openxmlformats-    
					        officedocument.spreadsheetml.sheet,application/vnd.ms-excel"
					 :auto-upload="false">
						<el-button type="text">导入</el-button>
					</el-upload>
				</el-button>
				<el-button type="text" @click="add" style="padding: 5px!important;">新增</el-button>
			</div>
		</div>

		<el-table :data="tableData" style="width: 100%">
			<el-table-column prop="p_id" label="用户ID" align='center'>
			</el-table-column>
			<el-table-column prop="prize_name" label="名称" align='center'>
			</el-table-column>
			<el-table-column label="中奖设置" align='center'>
				<template slot-scope="scope">
					<el-button type="text" v-if="scope.row.is_winning=='1'">是</el-button>
					<el-button type="text" v-else disabled>否</el-button>
				</template>
			</el-table-column>
			<el-table-column label="操作" align='center'>
				<template slot-scope="scope">
					<el-button size="mini" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
				</template>
			</el-table-column>
		</el-table>

		<pagination :currentPage="currentPage" :total="total" :limit="pagesize" :small="true" @handleCurrentChange="handleCurrentChange"
		 @handleSizeChange="handleSizeChange" />


		<el-dialog title="新增 :" :visible.sync="dialogVisible" width="30%">
			<div class="bt">
				<h5>名称：</h5><span>
					<el-input v-model="form.name" placeholder="请输入名称" type="text"></el-input>
				</span>
			</div>
			<div class="bt">
				<h5>设置中奖：</h5><span>
					<el-radio v-model="Is_radio" label="1">是</el-radio>
					<el-radio v-model="Is_radio" label="0">否</el-radio>
				</span>
			</div>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible=false">取 消</el-button>
				<el-button type="primary" @click="btn()">确 定</el-button>
			</span>
		</el-dialog>


		<el-dialog title="编辑 :" :visible.sync="dialogVisible_edit" width="30%">
			<div class="bt">
				<h5>名称：</h5><span>
					<el-input v-model="form.name" placeholder="请输入名称" type="text"></el-input>
				</span>
			</div>
			<div class="bt">
				<h5>设置中奖：</h5><span>
					<el-radio v-model="Is_radio" label="1">是</el-radio>
					<el-radio v-model="Is_radio" label="0">否</el-radio>
				</span>
			</div>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible_edit=false">取 消</el-button>
				<el-button type="primary" @click="btn_edit()">确 定</el-button>
			</span>
		</el-dialog>

	</div>
</template>

<script>
	import pagination from '../../components/Pagination.vue'
	export default {
		data() {
			return {
				currentPage: 1,
				pagesize: 10,
				total: 0,
				tableData: [],

				dialogVisible: false,
				form: {
					name: '',
					pass: ''
				},

				limitUpload: 1,

				input: '',
				value: true,
				Is_radio: '',

				dialogVisible_edit: false, //编辑
				item: {}

			}
		},
		components: {
			pagination
		},
		created() {
			this.All_data(this.currentPage, this.pagesize)
		},
		computed: {
			is_winning: function(e) {
				return e == 1 ? true : false
			}
		},
		methods: {
			add() {
				this.dialogVisible = true
				this.Is_radio = ''
				this.form.name = ''
			},
			// 新增事件
			btn() {
				let data = {
					"is_winning": this.Is_radio,
					"prize_name": this.form.name
				}
				// console.log(data)
				this.$axios.post(this.$url + '/choujiang/addPool', data)
					.then((res) => {
						if (res.data.result) {
							this.$message({
								message: '添加成功',
								showClose: true,
								type: 'success'
							});
							this.dialogVisible = false
							this.Is_radio = ''
							this.form.name = ''
							this.scrch_num = 2
							this.input = ''
							this.All_data(this.currentPage, this.pagesize)
						}
					})
					.catch((res) => {
						console.log(res)
					})
			},

			// 编辑
			handleEdit(index, item) {
				// console.log(item)
				this.dialogVisible_edit = true
				this.item = item
				this.Is_radio = String(item.is_winning)
				this.form.name = item.prize_name
			},
			btn_edit() {
				let data = {
					"p_id": this.item.p_id,
					"is_winning": this.Is_radio,
					"prize_name": this.form.name
				}
				// console.log(data)
				this.$axios.post(this.$url + '/choujiang/setPool', data)
					.then((res) => {
						if (res.data.result) {
							this.$message({
								message: res.data.msg,
								showClose: true,
								type: 'success'
							});
							this.dialogVisible_edit = false
							this.input = ''
							if (this.scrch_num == 1) {
								this.scrch_list(this.currentPage, this.pagesize)
							} else {
								this.currentPage = val;
								this.All_data(this.currentPage, this.pagesize);
							}
						}
					})
					.catch((res) => {
						console.log(res)
					})
			},

			handleDelete(index, item) {
				console.log(item.p_id)
				this.$axios.post(this.$url + '/choujiang/delPool', [item.p_id])
					.then((res) => {
						if (res.data.result) {
							this.$message({
								message: res.data.msg,
								showClose: true,
								type: 'success'
							});
							this.All_data(this.currentPage, this.pagesize)
						}
					})
					.catch((res) => {
						console.log(res)
					})
			},

			//搜索
			scrch() {
				this.input = ''
				this.scrch_num = 1
				this.currentPage = 1
				this.tableData = []
				this.total = 0
				this.handleCurrentChange(1)
			},
			scrch_list(page, size) {
				let data = {
					"prize_name": this.input,
					num: page,
					size: size
				}
				// console.log(data)
				this.$axios.post(this.$url + '/choujiang/searchPage', data)
					.then((res) => {
						if (res.data.result) {
							this.total = res.data.pojo.total
							this.tableData = res.data.pojo.list
						} else {
							this.$message({
								message: res.data.msg,
								showClose: true,
								type: 'error'
							});
						}
					})
					.catch((res) => {
						console.log(res)
					})
			},

			handleCurrentChange(val) {
				this.currentPage = val
				if (this.scrch_num == 1) {
					this.scrch_list(this.currentPage, this.pagesize)
				} else {
					this.currentPage = val;
					this.All_data(this.currentPage, this.pagesize);
				}
			},
			handleSizeChange(val) {
				this.pagesize = val;
				this.currentPage = 1; //这里选择每页多少数据，重置页码为1
				if (this.scrch_num == 1) {
					this.scrch(this.currentPage, this.pagesize)
				} else {
					this.All_data(this.currentPage, this.pagesize);
				}
			},
			// 获取列表
			All_data(page, size) {
				let data = {
					num: page,
					size: size
				}
				this.tableData = []
				this.$axios.post(this.$url + '/choujiang/searchPage', data)
					.then((res) => {
						// console.log(res.data)
						this.total = res.data.pojo.total
						this.tableData = res.data.pojo.list
					})
					.catch((res) => {
						console.log(res)
					})
			},

			//上传文件时处理方法  
			handleChange(file, fileList) {
				this.fileTemp = file.raw;
				if (this.fileTemp) {
					if ((this.fileTemp.type == 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') ||
						(this.fileTemp.type == 'application/vnd.ms-excel')) {
						this.importfxx(this.fileTemp);
					} else {
						this.$message({
							type: 'warning',
							message: '附件格式错误，请删除后重新上传！'
						})
					}
				} else {
					this.$message({
						type: 'warning',
						message: '请上传附件！'
					})
				}
			},
			//超出最大上传文件数量时的处理方法
			handleExceed() {
				this.$message({
					type: 'warning',
					message: '超出最大上传文件数量的限制！'
				})
				return;
			},
			//移除文件的操作方法
			handleRemove(file, fileList) {
				this.fileTemp = null
			},
			importfxx(obj) {
				let _this = this;
				let inputDOM = this.$refs.inputer;
				// 通过DOM取文件数据

				this.file = event.currentTarget.files[0];

				var rABS = false; //是否将文件读取为二进制字符串
				var f = this.file;

				var reader = new FileReader();
				//if (!FileReader.prototype.readAsBinaryString) {
				FileReader.prototype.readAsBinaryString = function(f) {
					var binary = "";
					var rABS = false; //是否将文件读取为二进制字符串
					var pt = this;
					var wb; //读取完成的数据
					var outdata;
					var reader = new FileReader();
					reader.onload = function(e) {
						var bytes = new Uint8Array(reader.result);
						var length = bytes.byteLength;
						for (var i = 0; i < length; i++) {
							binary += String.fromCharCode(bytes[i]);
						}
						//此处引入，用于解析excel
						var XLSX = require("xlsx");
						if (rABS) {
							wb = XLSX.read(btoa(fixdata(binary)), {
								//手动转化
								type: "base64"
							});
						} else {
							wb = XLSX.read(binary, {
								type: "binary"
							});
						}
						outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);
						//outdata就是读取的数据（不包含标题行即表头，表头会作为对象的下标）
						console.log(outdata)
						_this.$axios.post(_this.$url + '/choujiang/addPoolMore', outdata)
							.then((res) => {
								if (res.data.result) {
									_this.$message({
										message: '导入成功',
										showClose: true,
										type: 'success'
									});
									_this.All_data(_this.currentPage, _this.pagesize)
								}
							})
							.catch((res) => {
								console.log(res)
							})
						// _this.tableData = outdata
						// //此处可对数据进行处理
						// let arr = [];
						// outdata.map(v => {
						// 	let obj = {}
						// 	obj.code = v['Code']
						// 	obj.name = v['Name']
						// 	obj.pro = v['province']
						// 	obj.cit = v['city']
						// 	obj.dis = v['district']
						// 	arr.push(obj)
						// });
						// _this.da = arr;
						// _this.dalen = arr.length;
						// return arr;
					};
					reader.readAsArrayBuffer(f);
				};
				if (rABS) {
					reader.readAsArrayBuffer(f);
				} else {
					reader.readAsBinaryString(f);
				}
			}

		}
	}
</script>

<style scoped>
	.Lotterypool_box {
		width: 100%;
		height: auto;
		/* background: darkcyan; */
	}

	.Lotterypool_head {
		width: auto;
		height: auto;
		display: flex;
		/* padding: 5px 0; */
		margin: 10px 0 0 0;
		/* background: darkgoldenrod; */
		justify-content: space-between;
	}

	.Lotterypool_head_left {
		display: flex;
	}

	.Lotterypool_head_right {
		margin-right: 30px;
	}

	.el-dialog__body .bt {
		height: auto;
		line-height: 50px;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		padding: 10px 20px 10px 0;
		border-bottom: 1px solid #f0f0f0;
	}

	.bt h5 {
		float: left;
		width: 25%;
		text-align: center;
	}

	.bt span {
		width: 85%;
	}
</style>



